$primaryColorMain: #4BA2CC;
$primaryColorDark: #20578D;
$primaryColorLight: #E3F0F7;
$backgroundcolor: #F4F9FC;
$sensorIconSize:64px;
$sensorIconSizeHalf: 32px;

.heading{
  color:$primaryColorDark
}
.exploreContainer{

  display:flex;
  justify-content: space-between;
  position:relative;
  align-items: flex-start;
}

.exploreTitle{
  display:flex;
  align-items:center;
  font-size: 5rem;
  letter-spacing: 20px;
  margin-bottom:96px;
  font-family: "Bold";
  color:$primaryColorLight;
  margin-bottom:128px;
  position:relative;
  top:-3.4rem

}

.sensorPresentation{
  position:relative;
  max-width: 440px;
  width:100%;
}

.sensorIcon{
  width:$sensorIconSize;
  height:$sensorIconSize;
  position:absolute;

}

.sensorTop{
  top:-$sensorIconSize;
  left:calc(50% - #{$sensorIconSize/2});
}

.sensorTopRight{
  top: #{$sensorIconSize/2};
  right:0px;
}

.sensorBottomRight{
  top: #{$sensorIconSize*4};
  right:0px;;
}

.sensorBottom{
  left:calc(50% - #{$sensorIconSize/2});
  bottom:calc(#{$sensorIconSize} - #{$sensorIconSize/4});

}

.sensorBottomLeft{
  top: #{$sensorIconSize*4};
  left:0px;
}

.sensorTopLeft{
  top: #{$sensorIconSize/2};
  left:0px;
}

.exploreLeft{
  
  display:inline-block;
}

.exploreRight{
  display:inline-block;
  position:relative;
}

.explore.section{
  background-color: $backgroundcolor;
  z-index: -1;
}

