$primaryColorMain: #4BA2CC;
$primaryColorDark: #20578D;
$primaryColorLight: #F5FAFD;

.backgroundDecoration{
  background-color:$primaryColorLight;
  height:176px;
  width: 95%;
  border-top-right-radius: 90px;
  border-bottom-right-radius: 90px;
  position:absolute;
  left:-50%;
 

}

.giveawayContainer{
  display:flex;
  position:relative;
  align-items: flex-start;
}

.giveawayHeading{
  display:flex;
  align-items:center;
  font-size: 5rem;
  letter-spacing: 20px;
  margin-bottom:96px;
  font-family: "Heavy";
  color:$primaryColorMain;
}

.giveawayLeft{
  
  display:inline-block;
}

.giveawayRight{
  //display:inline-block;
  position:relative;
  top:-128px;
}